.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    position: fixed;
    z-index: 2;
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    img {
        width: 120px;
        height: 25px;
    }
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;

    li {
        margin: 0 1rem;
        flex-direction: column;

        div {
            width: 5px;
            height: 5px;
            background-color: transparent;
            border-radius: 50%;
            margin-bottom: 5px;
        }
    
        a {
            text-decoration: none;
            color: var(--gray-color);
            text-transform: uppercase;
            font-weight: 500;
            transition: all 0.3s ease-in-out;
            flex-direction: column;
    
            &:hover {
                color: var(--secondary-color);
            }
        }

        &:hover {
            div {
                background-color: var(--secondary-color);
            }
        }
    }

    @media screen and (max-width: 750px) {
        display: none;
    }
}

.app__navbar-menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secondary-color);
    cursor: pointer;
    
    svg {
        width: 70%;
        height: 70%;
        color: var(--white-color);
    }

    div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        margin-right: -300px;
        opacity: 0;
        outline: none;
        padding: 1rem;
        z-index: 5;
        width: 70%;
        height: 100vh;
        transition: all 0.6s ease-in-out;

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;

        background-color: var(--white-color);
        background-image: url(../../assets/bgWhite.png);
        background-size: cover;
        background-repeat: repeat;

        box-shadow: 0 0 20px rgba(168, 168, 168, 0.2);

        svg {
            width: 35px;
            height: 35px;
            color: var(--secondary-color);
            margin: 0.5rem 1rem;
        }

        ul {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
            list-style-type: none;

            li {
                margin: 1rem;

                a {
                    text-decoration: none;
                    color: var(--gray-color);
                    text-transform: uppercase;
                    font-size: 1rem;
                    font-weight: 500;
                    transition: all 0.3s ease-in-out;

                    &:hover {
                        color: var(--secondary-color);
                    }
                }
            }
        }

        @media screen and (max-width: 395px) {
            width: 80%;
        }
        @media screen and (max-width: 200px) {
            width: 90%;
        }
    }

    @media screen and (min-width: 750px) {
        display: none;
    }
}